.App {
    text-align: center;
  }
  body {
    padding: 0%;
    margin: 0%;
    font-family: Arial, Helvetica, sans-serif;
    height: auto;
    background-color: rgb(243, 219, 168);
  }

  
  nav {
    margin: 0;
    max-width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    text-decoration: none;
    padding-top: 10px;
    font-family: 'Assistant', sans-serif;
    margin-top: 10px;
  }
  
  .nav-link {
    display: inline-block;
    width: 45px;
    text-align: center;
    padding: 6px 10px;
    text-decoration: none;
    color: #333;
    font-family: 'Assistant', sans-serif;
    background-color: rgb(243, 219, 168);
    border: 3px solid rgb(255, 237, 215);
    border-radius: 30px;
    outline: none;
  }

  .nav-link:hover {
    background-color: rgb(234, 211, 161);
    border: 3px solid rgb(248, 222, 192);
    transition: 0.5s;
    color:white;
  }

  a {
    color: black;
    text-decoration: none;
    margin: 10px;
    font-size: 17px;
  }
  
  .createPostPage {
    width: 100%;
    height: calc(100vh - 80px);
    display: grid;
    place-items: center;
    margin-top: 70px;
    margin-bottom: 90px;
  }
  
  .cpContainer {
    max-height: 1000px;
    background-color: rgb(243, 219, 168);
    border: 4px solid rgb(255, 237, 215);
    border-radius: 12px;
    color: black;
    display: flex;
    flex-direction: column;
    width: 600px;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
    font-family: 'Assistant', sans-serif;
  }
  
  .cpContainer h1 {
    text-align: center;
  }
  
  .cpContainer label {
    font-size: 25px;
  }
  .cpContainer .inputGp {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .inputGp input .text-area-1,
  .inputGp textarea {
    font-size: 18px;
    border: none;
    border-radius: 2px;
    padding: 5px;
  }

  .text-area-1 {
    margin-top: 10px;
    padding-left: 10px;
  }

  .text-area-2 {
    margin-top: 10px;
  }
  
  .image-post {
    margin-top: 90px;
  }

  .inputGp input {
    height: 40px;
  }
  .inputGp textarea {
    height: 100px;
  }

  .text-area-quill {
    margin-top: 10px;
  }

  .tulisan-post {
    margin-top: 10px;
  }
  
  .cpContainer button {
    margin-top: 20px;
    height: 40px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
  }
  input,
  textarea {
    margin-top: 5px;
  }
  
  .loginPage {
    width: 100%;
    height: calc(100vh - 100px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .loginPage p {
    font-size: 30px;
    font-family: 'Shadows Into Light', cursive;
  }
  
  /* GOOGLE BUTTOn */
  .login-with-google-btn {
    cursor: pointer;
    transition: background-color 0.3s, box-shadow 0.3s;
    padding: 5px 16px 12px 42px;
    border: none;
    border-radius: 3px;
    box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
    color: #757575;
    font-size: 25px;
    font-weight: 500;
    font-family: "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    background-image: url(data:image/svg+xml;base64,);
    background-color: white;
    background-repeat: no-repeat;
    background-position: 12px 11px;
  }
  .login-with-google-btn:hover {
    box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25);
  }
  .login-with-google-btn:active {
    background-color: #eeeeee;
  }
  .login-with-google-btn:focus {
    outline: none;
    box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25),
      0 0 0 3px #c8dafc;
  }
  .login-with-google-btn:disabled {
    filter: grayscale(100%);
    background-color: #ebebeb;
    box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
    cursor: not-allowed;
  }
  
  .post {
    display: flex;
    flex-wrap: wrap; /* Menambahkan flex-wrap agar konten wrap ke baris baru pada perangkat kecil */
    align-items: center;
    text-align: center;
    margin-bottom: 20px;
    padding: 10px;
    border: 3px solid rgb(255, 237, 215);
    border-radius: 5px;
    width: 90%; /* Mengubah lebar menjadi persentase agar responsif */ /* Menambahkan batasan maksimal lebar */
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  
  .post-content {
    display: flex;
    width: 100%;
    font-family: 'Assistant', sans-serif;
  }
  
  .image-container {
    border: 3px solid rgb(204, 188, 168);;
    flex: 0 0 auto;
    width: 5em;
    height: 5em;
    top:0;
    display: none;
  }
  
  .post-info {
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    width:700px;
    margin-top: 20px;
    text-align: left;
  }
  
  .title-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    width: 100%;
    max-width: 800px;
  }
  
  .title-container h3 {
    margin: 0;
    font-size: 25px;
  }
  
  .underline {
    height: 1px;
    background-color: rgb(255, 237, 215);
    margin-bottom: 10px;
    width: 100%;
  }

  .post-text-position {
    width: 100%;
  }

  .post-text-1 {
    position: relative;
    text-align: left;
  }
  
  .post-text img {
    width: 100%;
    height: auto; /* Menghapus ukuran gambar yang ditentukan secara khusus */
  }
  
  .btn {
    margin-right: 10px;
  }
  

  .text-area-quill .ql-editor img {
    width: 100px;
    max-width: 500px; /* Atur lebar maksimum gambar */
    /* Tambahkan properti CSS lainnya jika diperlukan */
