  /* Footer */

  footer {
    background-color: rgb(243, 219, 168);
    padding: 20px;
    text-align: center;
  }

  .hero {
    margin-top: 85px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-family: 'Shadows Into Light', cursive;
  }
  
  .hero h1 {
