.post-container {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  margin-right: 20px;
  margin-left: 11.5px;
}

.post-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: rgb(243, 219, 168);
  border: 4px solid rgb(255, 237, 215);
  padding: 20px;
  border-radius: 4px;
  flex: 1;
  width: 90%;
  height: 490px;
  margin-top: 20px;
  cursor: pointer;
  margin: 0 auto; /* Tambahkan properti ini */
}

.post-image-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.post-image-1 img {
  width: 90%;
  height: 300px;
  object-fit: contain;
}

.post-info-1 {
  margin-top: 20px;
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 90%;
}

.post-title-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.post-title-1 h3 {
  font-size: 18px;
  font-weight: bold;
  overflow-wrap: break-word; /* Tambahkan properti ini */
}

.delete-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.delete-button i {
  color: red;
}

.underline {
  width: 100%;
  height: 1px;
  background-color: #ccc;
  margin-bottom: 10px;
}

.post-text {
  font-size: 14px;
  line-height: 1.5;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.read-more {
  color: #2a04ff;
  font-style: italic;
  cursor: pointer;
  text-decoration: none; /* Menambahkan gaya dasar untuk tautan */
}

.read-more:hover {
  text-decoration: underline; /* Menambahkan gaya saat tautan dihover */
}

.pagination {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin-top:60px;
}



.pagination .page-link {
    margin: 0 auto;
    display: block;
    padding: 10px;
    border: none;
    border-radius: 20px;
    background-color: rgb(243, 219, 168);
    border: 3px solid rgb(255, 237, 215);
    outline: none;
    width: 250px;
    max-width: 12em;
    cursor: pointer;
}

.pagination .page-link:hover {
  background-color: rgb(243, 219, 168);
    border: 3px solid rgb(255, 237, 215);
    transition: 0.5s;
    color:white;
}

.pagination .page-item.active .page-link {
  background-color: #007bff;
  color: #fff;
  border-color: #007bff;
}

.pagination .page-item.disabled .page-link {
  opacity: 0.6;
  cursor: not-allowed;
}

.pagination .page-item.disabled .page-link:hover {
  background-color: #f2f2f2;
}


/* Perbarui tata letak dengan media queries */
@media (max-width: 1400px) {
  .post-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 700px) {
  .post-container {
    grid-template-columns: repeat(1, 1fr);
  }
}
