.container {
    max-width: 600px;
    margin: 0 auto;
    width:90%;
    line-height: 1.5;
}

.text-center-0 {
    margin-top: 60px;
    text-align: center;
}

.text-center-2 {
    text-align: justify;
    margin-bottom: 80px;
    font-size: 1.1em;
    margin-top: 25px;
}

.text-center-3 {
    text-align: justify;
    margin-bottom: 40px;
    font-size: 1.1em;
    margin-top: 40px;
}

.text-center-4 {
    text-align: justify;
    margin-bottom: 30px;
    font-size: 1.1em;
    margin-top: 50px;
}

.text-center-1 {
    text-align: justify;
    margin-top: 35px;
    margin-bottom: 50px;
    font-size: 1.1em;
}

.img-responsive {
    max-width: 90%;
    height: auto;
    display: block;
    margin: 0 auto;
}

form {
    display: flex;
    justify-content: center;
}

.form-shape {
    width: 100%;
}

.form-shape p {
    margin-bottom: 0px;
}

.form-control {
    width: 93.8%;
    padding:10px;
    font-size: 16px;
    border-radius: 5px;
    background-color: rgb(243, 219, 168);
    border: 3px solid rgb(255, 237, 215);
}


.tombol-submit {
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 30px;
    color: #fff;
    background-color: white;
    border: 3px solid rgb(255, 237, 215);
    cursor: pointer;
    color: black;
}

.tombol-submit:hover {
    background-color: rgb(243, 219, 168);
    border: 3px solid rgb(255, 237, 215);
    transition: 0.5s;
    color: white;
    cursor: pointer;
}
