#box {
    height: auto;
    max-height: auto;
    background: rgb(243, 219, 168);
    background: rgb(243, 219, 168);
    background: rgb(243, 219, 168);
  }
  
  .firefly {
    width: 4px;
    height: 4px;
    background-color: rgb(238, 226, 197);
    animation: falling;
    border-radius: 100%;
    position: fixed;
  }
  
  @keyframes falling {
    50% {
      top: 105vh;
    }
    100% {
      top: -20px;
    }
  }
  
  @keyframes elevating {
    50% {
      top: -20px;
    }
    100% {
      top: 105vh;
    }
  }
  